import React, { useRef, useState } from 'react'
import axios from 'axios'

import classNames from 'classnames'
import styles from './ContactUsForm.module.scss'

const getDataObject = (formData, attributes = []) => {
  const obj = {}

  // eslint-disable-next-line no-restricted-syntax
  for (const attribute of attributes) {
    const item = formData.get(attribute)
    if (![undefined, null, '', {}, []].includes(item)) {
      obj[attribute] = item
    }
  }

  return obj
}

const ContactUsForm = () => {

  const [submitting, toggleSubmitting] = useState(false)
  const [isDone, toggleIsDone] = useState(false)

  const submitMessageForm = async e => {
    e.preventDefault()

    toggleSubmitting(true)

    const formData = getDataObject(
      new FormData(e.target), 
      ['name', 'email', 'companyName', 'phoneNumber', 'message']
    )
    try {
      const { target } = e
      const myApi = axios.create({
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
          // 'Access-Control-Allow-Origin': '*'
        }
      })
      await myApi.post('https://frdmcjj3g5.execute-api.us-east-1.amazonaws.com/dev/send-contact-us-email', formData)

      toggleSubmitting(false)
      toggleIsDone(true)

      setTimeout(() => {
        toggleIsDone(false)
      }, 2000)
      document.getElementById(target.getAttribute('id')).reset()
    } catch (err) {
      console.log('error', err.message) // eslint-disable-line
    }
  
  }

  return (
    <form id="contactUsForm" onSubmit={submitMessageForm} className={styles.contactUs}>
      <input name="name" autoComplete="off" type="text" className={styles.inputField} required placeholder="Name*" />
      <input name="email" autoComplete="off" type="email" className={styles.inputField} required placeholder="Email*" />
      <input name="companyName" autoComplete="off" type="text" className={styles.inputField} placeholder="Company Name" />
      <input name="phoneNumber" autoComplete="off" type="tel" className={styles.inputField} placeholder="Phone Number" />
      <textarea required name="message" className={styles.textareaField} minLength="20" maxLength="2000" placeholder="Your message here*... 20 characters Min." />
      <button className={classNames([styles.submitButton, submitting ? styles.loading : '', isDone ? styles.done : ''])} type="submit" />
    </form>
  )
}

export default ContactUsForm