
import React, { useRef, useEffect, useState } from 'react';
import './index/index.scss';

import Layout from './index/sections/Layout';
import Hero from './index/sections/Hero';
import MyJourney from './index/sections/MyJourney';
import MeetTheTeam from './index/sections/MeetTheTeam';
import MyProject from './index/sections/MyProject/MyProject'

const HomePage = () => {

  const [isActive1, updateIsActive1] = useState(false)
  const [isActive2, updateIsActive2] = useState(false)
  const [isActive3, updateIsActive3] = useState(false)
  const [isActive4, updateIsActive4] = useState(false)

  const pageIndicator = [
    {
      source: useRef(null),
      target: useRef(null),
      isActive: isActive1,
      label: 'Start here'
    },
    {
      source: useRef(null),
      target: useRef(null),
      isActive: isActive2,
      label: 'My Journey'
    },
    {
      source: useRef(null),
      target: useRef(null),
      isActive: isActive3,
      label: 'My Project'
    },
    {
      source: useRef(null),
      target: useRef(null),
      isActive: isActive4,
      label: 'Me & Contact'
    }
  ]

  useEffect(() => {
    handleScroll()
    document.addEventListener('scroll', handleScroll)
    return () => document.removeEventListener('scroll', handleScroll)
  })

  const handleScroll = (e) => {
    const heroTop = pageIndicator[0].target.current.getBoundingClientRect().top
    const journeyTop = pageIndicator[1].target.current.getBoundingClientRect().top
    const projectTop = pageIndicator[2].target.current.getBoundingClientRect().top
    const teamTop = pageIndicator[3].target.current.getBoundingClientRect().top

    const d = document.documentElement;
    const offset = d.scrollTop + window.innerHeight;
    const height = d.offsetHeight;

    clearActive()

    if (offset === height || teamTop <= 0) {
      updateIsActive4(true)
    } else if (projectTop <= 0) {
      updateIsActive3(true)
    } else if (journeyTop <= 0) {
      updateIsActive2(true)
    } else  {
      updateIsActive1(true)
    }
  }

  const clearActive = () => {
    updateIsActive1(false)
    updateIsActive2(false)
    updateIsActive3(false)
    updateIsActive4(false)
  }

  return (
    <Layout pageIndicator={pageIndicator}>
      <Hero targetRef={pageIndicator[0].target} teamRef={pageIndicator[1].target} />
      <MyJourney targetRef={pageIndicator[1].target} />
      <MyProject targetRef={pageIndicator[2].target} />
      <MeetTheTeam targetRef={pageIndicator[3].target} />
    </Layout>
  )
}

export default HomePage